body {
  width: 100vw;
  height: 100vh;
  margin: 0;
  padding : 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: antiquewhite;
  text-align: center;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  margin : 0;
  padding : 0;
}
.Navi {
  margin : 0;
  width : 20vw;
  height: 93vh;
  float : left;
  position: relative;
  background-color: #D9CDBC;
}
.Contents {
  margin : 0;
  width : 80vw;
  height: 93vh;
  float : left;
  position: relative;
  background-color: #E3E1E3;
}
.Footer {
  clear: left;
  height: 7vh;
  width: 100vw;
  background-color: #91877F;
}
.close {
    width: 96%;
    height: 3%;
    text-align: right;
    padding: 2%;
}
#closeImg {
    cursor: pointer;
}
#openImg {
    cursor: pointer;
    position: absolute;
    margin: 3%;
}
#logo {
    margin: 0 auto;
    height: 65%;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
}
#logoImg {
    min-width: 5%;
    min-height: 3%;
    max-width: 100%;
    max-height: 100%;
    cursor: pointer;
}
.loginForm {
    height: 30%;
}
.loginTable {
    padding:10px;
    margin:0 auto;
}
#loginFont {
    font-family: 'Gothic A1';
    color:#91877F;
    font-size: large;
    text-align: right;
    margin-top: 8px;
    margin-bottom: 8px;
    margin-right: 5px;
}
#loginSubmit {
    margin:7px;
    width: 7em;
    height: 3em;
    background-color: aliceblue;
    color:#91877F; 
    border: none;
}
#loginSubmit:hover {
    -webkit-animation-duration : 0.4s;
            animation-duration : 0.4s;
    -webkit-animation-name: loginSubmitAnim;
            animation-name: loginSubmitAnim;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
}
@-webkit-keyframes loginSubmitAnim {
    from {
        background-color: aliceblue;
        color:#91877F; 
    }
    to {
        background-color: #91877F;
        color:aliceblue; 
    }
}
@keyframes loginSubmitAnim {
    from {
        background-color: aliceblue;
        color:#91877F; 
    }
    to {
        background-color: #91877F;
        color:aliceblue; 
    }
}
#button {
    text-align: right;
}
.box {
    margin: 0;
    margin-right: 0;
    width: 9em;
    height: 9em;
    line-height: 7em;
    text-align: center;
    border-radius: 100%;
    border: solid 1px #F0F2F3;
    background-color: #F0F2F3;
    color: #91877F;
    position: relative;
    cursor: pointer;
}
.font {
    font-family: 'Gothic A1';
    font-weight: bold;
    font-size: 20px;
}
.Profile {
    top : 4%;
    left : 65%;
}

.GoogleDrive {
    top : 8%;
    left : 20%;
}

.Board {
    top : 12%;
    left : 60%;
}
.Lab {
    top : 16%;
    left: 18%;
}
.box:hover {
    -webkit-animation-duration: 0.5s;
            animation-duration: 0.5s;
    -webkit-animation-name: box-hover;
            animation-name: box-hover;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
}
@-webkit-keyframes box-hover {
    from {

    }
    to {
        background-color: #CDC5C2;
        color: #F0F2F3;
    }
}
@keyframes box-hover {
    from {

    }
    to {
        background-color: #CDC5C2;
        color: #F0F2F3;
    }
}
.resetButtonDiv {
    position: relative;
    top: 15%;
}
.resetButton {
    font-family: 'Gothic A1';
    font-size: 2em;
    border-radius: 7%;
    cursor: pointer;
}
.ProfileContent {
    margin: 3%;
    text-align: left;
    width: 94%;
    height: 94%;
}
.Profile1 {
    width: 100%;
    height: 30%;
}
.ProfileLogo {
    width: 30%;
    height: 100%;
    float : left;
    -webkit-flex-direction: column;
            flex-direction: column;
    text-align: center;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
}
#profileImg {
    padding: 1%;
    border: 1px solid #91877F;
    border-radius: 100%;
    width: 15em;
    height: 15em;
    max-width: 50%;
    max-height: 80%;
}
#logoAlt {
    font-family: 'Noto Sans KR';
}
.List {
    font-family: 'Noto Sans KR';
    margin: 3%;
    width : 100%;
    font-size: 1rem;
}
#profileList {
    margin: 3%;
    list-style-type: none;
}
.first {
    font-size: 1.2rem;
    text-align: left;
    font-weight: bold;
    color: #5F555F;
}
.elmt {
    padding-right: 50px;
}
.link {
    text-decoration: none;
}
.link:visited, .link:active {
    color: black;
}
.link:hover {
    color :darkgrey;
}
.ProfileInfo {
    width: 70%;
    height: 100%;
    float : left;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
}
.ProfileTable {
    width: 100%;
    height: 66%;
    clear: both;
}
.PortfolioTitle {
    width: 100%;
    height: 5%;
    text-align: center;
}
#Ptitle {
    font-size: 1.7rem;
    font-family: 'Noto Sans KR';
    color: #5F555F;
    font-weight: bold;
}
.PortfolioContents {
    width: 100%;
    height: 95%;
}
.Footer {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    justify-items: center;
    color: #F0F2F3;
    font-family: 'Gothic A1';
}
.Contact {
    text-align: left;
}
.contactImg {
    fill: #E3E1E3;
}
.Administer {
    text-align: left;
}
.page {
    text-decoration: none;
}
.page:hover {
    color: #91877F;
}
.page:active, .page:focus, .page:checked, .page:link, .page:visited {
    color: #F0F2F3;
}
